import React, { useEffect, useState } from 'react'
import { Link, useParams, useNavigate } from "react-router-dom";
import { Row, Col, Image, ListGroup, Card, Button ,Form} from 'react-bootstrap';
import Rating from '../comp/rating';
import { useDispatch, useSelector } from "react-redux"
import { ListProuctsDetails, createProductReview } from "../actions/Productactions"
import Message from '../comp/Message'
import Spiner from '../comp/Loader'
import { PRODUCT_CREATE_REVIEW_RESET } from "../constants/Productconstant"


const Productscreen = () => {
    const navigate = useNavigate()
    const [qty, setQty] = useState(1)
    const [rating, setRating] = useState(0)
    const [comment, setComment] = useState('')
    const param = useParams()
    const id = param.id
    const dispatch = useDispatch()
    const { userInfo } = useSelector(state => state.userLogin)
    const { error, Loading, product } = useSelector(state => state.productDetails)
    const { success: successProductReview, loading: loadingProductReview, error: errorProductReview } = useSelector(state => state.productReviewCreate)
    useEffect(() => {
        // console.log(`useeffact`);
        dispatch(ListProuctsDetails(id))
        if (successProductReview) {
            alert("Submitted Review")
            setRating(0)
            setComment('')
            dispatch({ type: PRODUCT_CREATE_REVIEW_RESET })
        }


    }, [id, dispatch, successProductReview])
    // console.log(product);
    const addToCart = () => {

        navigate(`/cart/${id}?qty=${qty}`)
    }

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(createProductReview(id, { rating, comment }))
    }
    return (

        <div>
            <>
                <Link className='btn btn-light my-3' to='/'>
                    Go Back
                </Link>
                {
                    Loading ? <Spiner /> : error ? <Message varient='danger'>{error}</Message> :

                        <>
                            <Row>
                                <Col md={6}>
                                    <Image src={product.image} alt={product.name} fluid />
                                </Col>
                                <Col md={3}>
                                    <ListGroup variant="flush">
                                        <ListGroup.Item>
                                            <h2>{product.name}</h2>
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            <Rating value={product.rating} text={`${product.numReviews}reviews`} />
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            price:${product.price}
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                            Description:${product.description}
                                        </ListGroup.Item>
                                    </ListGroup>
                                </Col>
                                <Col md={3}>
                                    <Card>
                                        <ListGroup variant="flush">
                                            <ListGroup.Item>
                                                <Row>
                                                    <Col>
                                                        price:
                                                    </Col>
                                                    <Col>
                                                        <strong>${product.price}</strong>
                                                    </Col>
                                                </Row>
                                            </ListGroup.Item>


                                            <ListGroup.Item>
                                                <Row>
                                                    <Col>
                                                        status:
                                                    </Col>
                                                    <Col>
                                                        {product.countInStock > 0 ? "In Stock" : "Out of Stock"}
                                                    </Col>

                                                </Row>
                                            </ListGroup.Item>
                                            <ListGroup.Item>

                                                {product.countInStock > 0 && (
                                                    <ListGroup.Item>
                                                        <Row>
                                                            <Col>Qty</Col>
                                                            <Col>
                                                                {/* <Form.Control as="select" value={qty} onChange={(e) =>
                                                                setQty(e.target.value)}>
                                                                {
                                                                    [...Array(product.countInStock).keys()].map((x) => (
                                                                        <option key={x + 1} value={x + 1}>
                                                                            {x + 1}
                                                                        </option>))
                                                                }
                                                            </Form.Control> */}
                                                                <select value={qty} onChange={(e) => setQty(e.target.value)} >
                                                                    {
                                                                        [...Array(product.countInStock).keys()].map((ele, i) => {
                                                                            // console.log(ele);
                                                                            return <option key={i} value={ele + 1}>{ele + 1}</option>
                                                                        })
                                                                    }

                                                                </select>
                                                            </Col>
                                                        </Row>
                                                    </ListGroup.Item>)}


                                            </ListGroup.Item>


                                            <ListGroup.Item>
                                                <Button className="btn-block" type="button" disabled={product.countInStock === 0} onClick={addToCart}>
                                                    Add to Cart
                                                </Button>
                                            </ListGroup.Item>
                                        </ListGroup>
                                    </Card>
                                </Col>
                            </Row>


                            <Row>
                                <Col md={6}>
                                    {product.reviews.length === 0 && <Message>No Reviews</Message>}
                                    <ListGroup variant='flush'>
                                        {product.reviews.map((review) => (
                                            <ListGroup.Item key={review._id}>
                                                <strong>{review.name}</strong>
                                                <Rating value={review.rating} />
                                                <p>{review.createdAt.substring(0, 10)}</p>
                                                <p>{review.comment}</p>
                                            </ListGroup.Item>
                                        ))}
                                        <ListGroup.Item>
                                            <h2>Write a Customer Review</h2>
                                            {successProductReview && (
                                                <Message variant='success'>
                                                    Review submitted successfully
                                                </Message>
                                            )}
                                            {loadingProductReview && <Spiner/>}
                                            {errorProductReview && (
                                                <Message variant='danger'>{errorProductReview}</Message>
                                            )}
                                            {userInfo ? (
                                                <Form onSubmit={submitHandler}>
                                                    <Form.Group controlId='rating'>
                                                        <Form.Label>Rating</Form.Label>
                                                        <Form.Control
                                                            as='select'
                                                            value={rating}
                                                            onChange={(e) => setRating(e.target.value)}
                                                        >
                                                            <option value=''>Select...</option>
                                                            <option value='1'>1 - Poor</option>
                                                            <option value='2'>2 - Fair</option>
                                                            <option value='3'>3 - Good</option>
                                                            <option value='4'>4 - Very Good</option>
                                                            <option value='5'>5 - Excellent</option>
                                                        </Form.Control>
                                                    </Form.Group>
                                                    <Form.Group controlId='comment'>
                                                        <Form.Label>Comment</Form.Label>
                                                        <Form.Control
                                                            as='textarea'
                                                            row='3'
                                                            value={comment}
                                                            onChange={(e) => setComment(e.target.value)}
                                                        ></Form.Control>
                                                    </Form.Group>
                                                    <Button
                                                        disabled={loadingProductReview}
                                                        type='submit'
                                                        variant='primary'
                                                    >
                                                        Submit
                                                    </Button>
                                                </Form>
                                            ) : (
                                                <Message>
                                                    Please <Link to='/login'>sign in</Link> to write a review{' '}
                                                </Message>
                                            )}
                                        </ListGroup.Item>
                                    </ListGroup>
                                </Col>
                            </Row>
                        </>
                }
            </>

        </div>
    )
}
export default Productscreen


















